import { HeaderStyled, StyledLink } from "./HeaderStyled"
import { HeaderContainer } from "./HeaderStyled"
import { useSelector, useDispatch } from "react-redux"
import { setUserInfo } from "redux/actions"
export const Header = () => {
    const userInfo = useSelector(state => state.userInfo);
    const dispatch = useDispatch();
    const clickHendler = (event) => {
        dispatch(setUserInfo({}))
    }
    return <HeaderStyled>
        <HeaderContainer>
            <h1> </h1>
            <nav>
                <ul>
                    <li><StyledLink to="/" end></StyledLink></li>
                    {!userInfo.email ? 
                    <li><StyledLink to="autorisation" end></StyledLink></li>:
                    <>
                        <li><StyledLink to="usages"></StyledLink></li>
                        <li><StyledLink to="statistic"></StyledLink></li>
                        <li><button  onClick={clickHendler}></button></li>
                    </>
                    }
                    <li><StyledLink to="calculator"></StyledLink></li>
                </ul>
            </nav>
        </HeaderContainer>
    </HeaderStyled>
}

